var XMLtoJSONstream = require('./lib/xmlToJsonStream');

var traverse = require('./lib/xmlToJson');

var cleanXML = require('./lib/cleanXML');

var validator = require('./lib/validator');

module.exports = function (options) {
  var opts = options || {};
  var attributeMode = typeof opts.attributeMode === 'undefined' ? true : opts.attributeMode;

  var createStream = function createStream() {
    return new XMLtoJSONstream({
      attributeMode: attributeMode
    });
  };

  var xmlToJson = function xmlToJson(xml, cb) {
    var clean = cleanXML(xml);

    if (!validator(clean)) {
      var err = new Error('Invalid XML. XML is missing closing or opening tag');
      return cb(err);
    }

    var json;

    try {
      json = traverse(clean, attributeMode);
      return cb(null, json);
    } catch (e) {
      return cb(e);
    }
  };

  return {
    createStream: createStream,
    xmlToJson: xmlToJson
  };
};